* {
  outline: none;
}

html, body {
  height: 100%;
  min-height: 100%;
}

body {
  margin: 0;
}

.tb {
  width: 100%;
  display: table;
}

.td {
  vertical-align: middle;
  display: table-cell;
}

input, button {
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  margin: 0;
  padding: 0;
  font-family: Nunito;
}

#cover {
  width: 800px;
  background-color: #75dbff;
  border-radius: 20px;
  margin: -20px auto;
  padding: 35px;
  transform: scale(.3);
  box-shadow: 0 10px 40px #75dbff, 0 0 0 20px rgba(255, 255, 255, .92);
}

form {
  height: 96px;
}

input[type="text"] {
  width: 100%;
  height: 96px;
  font-size: 90px;
  line-height: 1;
}

input[type="text"]::-ms-input-placeholder {
  color: #22acdc;
}

input[type="text"]::placeholder {
  color: #22acdc;
}

#s-cover {
  width: 1px;
  padding-left: 35px;
}

button {
  width: 84px;
  height: 96px;
  cursor: pointer;
  display: block;
  position: relative;
}

#s-circle {
  width: 43px;
  height: 43px;
  background-color: rgba(0, 0, 0, 0);
  border: 15px solid #fff;
  border-radius: 50%;
  margin-top: 0;
  transition: all .5s;
  position: relative;
  top: -8px;
  left: 0;
}

button span {
  width: 45px;
  height: 15px;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
  transition: all .5s;
  display: block;
  position: absolute;
  top: 68px;
  left: 43px;
  transform: rotateZ(52deg);
}

button span:before, button span:after {
  content: "";
  width: 45px;
  height: 15px;
  transform: rotateZ(0);
  background-color: #fff;
  border-radius: 10px;
  transition: all .5s;
  position: absolute;
  bottom: 0;
  right: 0;
}

#s-cover:hover #s-circle {
  width: 67px;
  height: 15px;
  background-color: #fff;
  border-width: 0;
  border-radius: 20px;
  top: -1px;
}

#s-cover:hover span {
  width: 25px;
  transform: rotateZ(0);
  margin-top: -9px;
  top: 50%;
  left: 56px;
}

#s-cover:hover button span:before {
  bottom: 11px;
  transform: rotateZ(52deg);
}

#s-cover:hover button span:after {
  bottom: -11px;
  transform: rotateZ(-52deg);
}

#s-cover:hover button span:before, #s-cover:hover button span:after {
  width: 40px;
  background-color: #fff;
  right: -6px;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.photo-card {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.photo-card:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 80%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
  display: block;
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.info {
  height: 20%;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.info-item {
  color: #fff;
  width: 20%;
  height: 30px;
  text-align: center;
  background-color: #75dbff;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 4px;
  font-family: Nunito;
  font-size: 12px;
  display: flex;
  box-shadow: 0 2px 4px #75dbff, 0 0 0 4px rgba(255, 255, 255, .92);
}

